<template>
  <div class="track">
    <div class="minus-button">
      <span class="minus-icon" @click="CountDecrease">-</span>
    </div>
      <label class="track__count">{{countNumber}}</label>
    <div class="plus-button">
      <span class="plus-icon" @click="CountIncrease">+</span>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      countNumber: 0
    }
  },
  props: ['count'],
  created () {
    this.countNumber = this.count
  },
  watch: {
    count () {
      this.countNumber = this.count
    }
  },
  methods: {
    CountIncrease: function () {
      this.countNumber = parseInt(this.countNumber) + 1
      this.$emit('changeCount',this.countNumber)
    },
    CountDecrease: function () {
      this.countNumber = parseInt(this.countNumber) > 0 ? parseInt(this.countNumber) - 1 : 0
      this.$emit('changeCount',this.countNumber)
    }
  }
}
</script>
<style scoped lang="scss">
  .track {
    display: flex;
    align-items: center;
    .track__count {
      margin: 0 5px;
    }
    .minus-button,
    .plus-button{
      height: 16px;
      width: 16px;
      display: block;
      border: 1px solid #9BA0AD;
      border-radius: 50%;
      text-align: center;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        width: 16px;
        height: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
</style>
